*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

main {
  transition: all var(--duration) var(--timeFunc);
  padding-top: 115px;
}

header {
  transition: all var(--duration) var(--timeFunc);
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  color: var(--primary-text-color);
  background-color: #f4f4f4;
  margin: 0;
  font-family: Manrope, sans-serif;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

a {
  text-decoration: none;
}

.list {
  list-style: none;
}

.button {
  text-align: center;
  cursor: pointer;
  border-color: #0000;
  font-family: inherit;
}

.button:hover {
  cursor: pointer;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
}

@media screen and (min-width: 320px) and (max-width: 479px) {
  .container {
    width: 100%;
  }
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

html.dark body {
  color: #f4f4f4;
  background-color: #111321;
}

html.light body {
  color: #111321;
  background-color: #f4f4f4;
}

:root {
  --light-color: #f4f4f4;
  --dark-color: #2e2e2e;
  --main-color: #111321;
  --secondary-color: #4440f6;
  --third-color: #00b91266;
  --duration: .25s;
  --timeFunc: cubic-bezier(.4, 0, .2, 1);
  --light-theme-color: #f4f4f4;
  --primary-text-color: #111321;
  --secondary-text-color: #11132166;
  --accent-color: #4440f6;
  --dark-theme-color: #2e2e2e;
  --header-border-bottom-color: #0003;
}

.filter-wrapper {
  column-gap: 10px;
  margin-top: 30px;
  margin-bottom: 60px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .filter-wrapper--categories {
    flex-basis: 75%;
  }
}

@media screen and (min-width: 1280px) {
  .filter-wrapper--categories {
    flex-basis: 68%;
  }
}

.filter-wrapper--categories.filters.dark-theme {
  color: #4440f6;
  background-color: #111321;
}

.filter-wrapper--categories.filters.dark-theme .filters--category {
  background-color: #f8f8f8;
}

.filter-wrapper--categories.filters.dark-theme .filters--category.active-category, .filter-wrapper--categories.filters.dark-theme .filters--category:hover, .filter-wrapper--categories.filters.dark-theme .filters--category:focus {
  color: var(--light-color);
  background-color: var(--secondary-color);
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .filters {
    flex-wrap: wrap;
    display: flex;
  }
}

.filters--category {
  text-align: center;
  color: var(--secondary-color);
  cursor: pointer;
  border: 1px solid var(--secondary-color);
  transition: all var(--duration) var(--timeFunc);
  border-radius: 100px;
  padding: 8px 24px;
  font-size: 12px;
  line-height: 1.33;
  display: inline-block;
}

.filters--category:not(:last-child) {
  margin-right: 16px;
}

.filters--category:hover, .filters--category:focus {
  color: var(--light-color);
  background-color: var(--secondary-color);
}

.filters--others {
  max-height: 50vh;
  opacity: 0;
  z-index: -10;
  filter: drop-shadow(0 10px 60px #0000001a);
  color: var(--secondary-color);
  transition: all var(--duration) var(--timeFunc);
  background: #f8f8f8;
  border-radius: 20px;
  flex-direction: column;
  row-gap: 12px;
  padding: 14px 24px;
  font-size: 12px;
  line-height: 1.33;
  display: inline-flex;
  position: absolute;
  top: 102%;
  overflow-y: scroll;
}

.filters--others.isOpen {
  z-index: 10;
  opacity: 1;
}

.filters--others .other-category {
  cursor: pointer;
  transition: all var(--duration) var(--timeFunc);
}

.filters--others .other-category:focus, .filters--others .other-category:hover {
  text-decoration: underline;
}

.filters .active-category {
  color: var(--light-color);
  background-color: var(--secondary-color);
}

.news-not-found--container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 120px;
  display: flex;
}

.header {
  width: 100%;
  z-index: 50;
  background: var(--light-color);
  border-bottom: 1px solid #0003;
  position: fixed;
  top: 0;
  left: 0;
}

.header:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
}

.header.dark-theme {
  color: #f8f8f8;
  background-color: #111321;
  border-bottom: 1px solid #f4f4f433;
}

.header__body {
  height: 81px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.header__logo {
  color: inherit;
  z-index: 5;
  cursor: pointer;
  font-weight: 700;
  line-height: 1.375;
  position: relative;
}

.header__logo.dark-theme {
  color: var(--light-theme-color);
}

.header__list {
  z-index: 2;
  color: var(--primary-text-color);
  gap: 30px;
  display: flex;
  position: relative;
}

.header__link {
  color: inherit;
  transition: color var(--duration) var(--timeFunc);
  padding: 10px 0;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.375;
}

.header__link.current {
  color: var(--accent-color);
  position: relative;
}

.header__link.current:after {
  content: "";
  width: 100%;
  height: 2px;
  background: var(--accent-color);
  border-radius: 2px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media screen and (min-width: 768px) {
  .header__link.current:after {
    margin: -2px;
  }
}

.header__list-icon {
  display: none;
}

.header__search {
  z-index: 3;
  width: 31px;
  height: 31px;
  background-color: var(--light-theme-color);
  z-index: 3;
  border-radius: 30px;
  align-items: center;
  transition: all .5s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.header__search input {
  background-color: var(--light-theme-color);
}

.header__search input::placeholder {
  color: var(--secondary-text-color);
}

@media screen and (max-width: 767px) {
  body.lock {
    overflow: hidden;
  }

  .header__body {
    height: 106px;
  }

  .header__logo {
    font-size: 24px;
  }

  .header__menu {
    width: 100%;
    height: 100%;
    background-color: var(--light-theme-color);
    padding: 125px 0 0;
    transition: all .5s ease-in-out;
    position: fixed;
    top: -100%;
    left: 0;
    overflow: auto;
  }

  .header__menu.dark-theme {
    background-color: var(--primary-text-color);
    color: var(--light-theme-color);
  }

  .header__menu.active {
    top: 0;
  }

  .header__list, .header__list-icon {
    display: block;
  }

  .header__list li {
    align-items: center;
    gap: 14px;
    padding-top: 6px;
    padding-bottom: 6px;
    padding-left: 16px;
    display: flex;
  }

  .header__list li:focus, .header__list li:focus-within {
    background-color: var(--accent-color);
  }

  .header__list li:focus a, .header__list li:focus-within a {
    color: var(--light-theme-color);
  }

  .header__link {
    align-items: center;
    gap: 15px;
    font-size: 16px;
    font-weight: 500;
    display: flex;
  }

  .header__search .search__icon {
    width: 31px;
    height: 31px;
    background-color: var(--light-theme-color);
    z-index: 999;
    cursor: pointer;
    border-radius: 60px;
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
  }

  .header__search .search__icon:before {
    content: "";
    width: 18px;
    height: 18px;
    border: 2px solid var(--primary-text-color);
    border-radius: 50%;
    position: absolute;
    transform: translate(-2px, -2px);
  }

  .header__search .search__icon:after {
    content: "";
    width: 2px;
    height: 9px;
    background-color: var(--primary-text-color);
    position: absolute;
    transform: translate(7px, 7px)rotate(-45deg);
  }

  .header__search.active {
    outline: 1px solid var(--primary-text-color);
    width: 173px;
  }

  .header__search .search__input {
    width: 190px;
    height: 31px;
    justify-content: center;
    align-items: center;
    display: flex;
    position: relative;
    left: 31px;
  }

  .header__search .search__input input {
    width: 100%;
    height: 100%;
    border: 0;
    outline: 0;
    padding: 10px 0;
    font-size: 12px;
    position: absolute;
    top: 0;
    left: 0;
  }

  .header__burger {
    cursor: pointer;
    width: 26px;
    height: 20px;
    z-index: 3;
    display: block;
    position: relative;
  }

  .header__burger span {
    background-color: var(--primary-text-color);
    width: 100%;
    height: 3px;
    border-radius: 2px;
    transition: all .5s ease-in-out;
    position: absolute;
    top: 9px;
    left: 0;
  }

  .header__burger:before, .header__burger:after {
    content: "";
    background-color: var(--primary-text-color);
    width: 100%;
    height: 3px;
    border-radius: 2px;
    transition: all .5s ease-in-out;
    position: absolute;
    left: 0;
  }

  .header__burger:before {
    top: 0;
  }

  .header__burger:after {
    bottom: 0;
  }

  .header__burger.active span {
    transform: scale(0);
  }

  .header__burger.active:before {
    top: 9px;
    transform: rotate(45deg);
  }

  .header__burger.active:after {
    bottom: 9px;
    transform: rotate(-45deg);
  }

  .search-burger-container {
    align-items: center;
    gap: 14px;
    display: flex;
  }
}

@media screen and (min-width: 768px) {
  .header__body {
    height: 113px;
  }

  .header__logo {
    font-size: 32px;
  }

  .header__link {
    font-size: 16px;
    font-weight: 700;
  }

  .header__search {
    outline: 1px solid var(--primary-text-color);
    width: 213px;
    position: relative;
  }

  .header__search .search__icon {
    width: 31px;
    height: 31px;
    background-color: var(--light-theme-color);
    z-index: 999;
    cursor: pointer;
    border-radius: 60px;
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
  }

  .header__search .search__icon:before {
    content: "";
    width: 15px;
    height: 15px;
    border: 2px solid var(--primary-text-color);
    border-radius: 50%;
    position: absolute;
    transform: translate(-2px, -2px);
  }

  .header__search .search__icon:after {
    content: "";
    width: 2px;
    height: 7px;
    background-color: var(--primary-text-color);
    position: absolute;
    transform: translate(5px, 5px)rotate(-45deg);
  }

  .header__search .search__input {
    width: 190px;
    height: 31px;
    justify-content: center;
    align-items: center;
    display: flex;
    position: relative;
    left: 31px;
  }

  .header__search .search__input input {
    width: 100%;
    height: 100%;
    border: 0;
    outline: 0;
    padding: 10px 0;
    font-size: 12px;
    position: absolute;
    top: 0;
    left: 0;
  }

  .header__burger {
    display: none;
  }
}

@media screen and (min-width: 1280px) {
  .header__link {
    font-size: 18px;
    font-weight: 700;
    line-height: 1.39;
  }

  .header__link:hover, .header__link:focus {
    color: var(--secondary-color);
    width: 100%;
    outline: none;
  }

  .header__search {
    width: 288px;
  }

  .header__burger {
    display: none;
  }
}

.toggle input[type="checkbox"] {
  display: none;
}

.toggle label {
  z-index: 100;
  background-color: var(--light-color);
  cursor: pointer;
  width: 40px;
  height: 20px;
  border: .6px solid #4b48db;
  border-radius: 20px;
  transition: all .3s ease-in-out;
  display: inline-block;
  position: relative;
}

.toggle label:after {
  background-color: var(--secondary-color);
  content: " ";
  cursor: pointer;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  transition: all .3s ease-in-out;
  display: inline-block;
  position: absolute;
  top: 1.5px;
  left: 1px;
}

.toggle input[type="checkbox"]:checked ~ label {
  background-color: var(--secondary-color);
  border-color: var(--light-color);
}

.toggle input[type="checkbox"]:checked ~ label:after {
  background-color: var(--light-color);
  transform: translateX(20px);
}

.header__list {
  color: inherit;
}

.toggle {
  column-gap: 10px;
  display: flex;
}

.filter-wrapper--date .date {
  flex-direction: row;
